/*>>>>>>>> Reset <<<<<<<<<*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Rubik', sans-serif;
  font-weight: 400;
  color: #555555;
  font-size: 14px;
  line-height: 20px;
  width: 100%;
}

.site-wrapper {
  position: relative;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: .5rem;
  padding: 0;
  line-height: 1.1;
}

a,
span {
  display: inline-block;
}

a {
  transition: 0.4s;
  color: inherit;
}

a:focus {
  outline: none !important;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

a:hover {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
}

img {
  max-width: 100%;
}

p {
  line-height: 20px;
}

.btn:focus,
.btn:active {
  box-shadow: none;
  border-color: transparent;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
}

button:focus {
  outline: none;
}

.icon i {
  font-size: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

.card {
  border: none;
  border-radius: 0;
}

/* Slick Slider Resets */
.slick-slide:focus {
  outline: none;
}

.slick-slide:focus a:focus {
  outline: none;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

/* No gutters */
@media (min-width: 992px) {
  .no-gutters-lg > .col,
  .no-gutters-lg > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

@media (min-width: 1200px) {
  .no-gutters-lg > .col,
  .no-gutters-lg > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

@media (min-width: 1500px) {
  .no-gutters-lg > .col,
  .no-gutters-lg > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

/*List Vewing Toolbar*/
.shop-product-wrap.list [class*="col"] {
  flex: 100%;
  max-width: 100%;
}

.nice-select {
  display: flex;
  align-items: center;
  float: none;
}

#scrollUp {
  bottom: 80px;
  right: 10px;
  padding: 10px 15px;
  text-align: center;
  cursor: pointer;
  border: 2px solid #ebebeb;
  color: #292929;
  transform: rotate(-90deg);
  font-weight: 600;
  text-transform: uppercase;
  background: rgba(255, 255, 255, 0.9);
  z-index: 9;
  font-size: 12px;
  transition: .4s;
}

@media (min-width: 768px) {
  #scrollUp {
    right: -40px;
    padding: 10px 20px 10px 10px;
  }
}

#scrollUp span {
  margin-right: 15px;
  display: none;
}

@media (min-width: 768px) {
  #scrollUp span {
    display: inline-block;
  }
}

#scrollUp:hover {
  color: #fff;
  background: #24bbdb;
  border-color: #24bbdb;
}

