  font-weight: 600;
  max-width: 183px;
  width: 100%;
  border-radius: 0px;
  text-transform: uppercase;
}

.btn-outlined {
  background: transparent;
  border: 2px solid #ececec !important;
}

.btn-outlined:hover {
  border: 2px solid #333 !important;
  background: #333;
  color: #fff;
}

