}

.btn-outlined--white:hover {
  background: #24bbdb;
  border-color: #24bbdb;
  color: #fff;
}

.btn-outlined--black {
  background: transparent;
  border: 1px solid #333;
  color: #333;
}

.btn-outlined--black:hover {
  background: #24bbdb;
  border-color: #24bbdb;
  color: #fff;
}

