}

.btn--primary:hover {
  background: #333;
  color: #fff;
}

.btn-black {
  height: 50px;
  padding: 0 30px;
  display: inline-flex;
  align-items: center;
  color: #fff;
  background: #333;
  font-size: 13px;
  font-weight: 500;
  font-family: Rubik, Arial, Helvetica, sans-serif;
  box-shadow: none;
  border-radius: 3px;
  border: none;
  outline: none;
  text-shadow: none;
  text-transform: uppercase;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  vertical-align: middle;
  white-space: nowrap;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
}

