  margin-right: 10px;
  cursor: pointer;
  position: relative;
  margin-bottom: 0;
  display: inline-block;
}

.rating-widget-block label:last-child {
  margin-right: 0;
}

.rating-widget-block label:before {
  content: "\f005";
  font-family: "Font Awesome 5 Free";
  position: relative;
  display: block;
  font-size: 16px;
  color: #e3e3e3;
  font-weight: 900;
}

.rating-widget-block label:after {
  content: "\f005";
  font-family: "Font Awesome 5 Free";
  position: absolute;
  display: block;
  font-size: 16px;
  color: #fec701;
  font-weight: 900;
  top: 0;
  left: 0;
  opacity: 0;
  transition: 0.4s;
}

.rating-widget-block label:hover ~ label:after,
.rating-widget-block label:hover:after,
.rating-widget-block input:checked ~ label:after {
  opacity: 1;
}

