  position: relative;
  background-color: #f6f6f6;
  border-radius: 50px;
  display: inline-block;
  width: 170px;
  padding: 10px 25px 10px 56px;
  transition: left 0.7s cubic-bezier(0.77, -1.5, 0.12, 3) 0s, color 0.4s ease-in;
}

.compare-table .table tbody tr td.pro-addtocart .add-to-cart i {
  width: 41px;
  height: 41px;
  background-color: #fff;
  border-radius: 50px;
  display: block;
  float: left;
  font-size: 20px;
  text-align: center;
  line-height: 41px;
  box-shadow: 0 3px 7px rgba(41, 41, 41, 0.3);
  transition: left 0.3s ease 0s, color .4s ease-in;
  position: absolute;
  left: 0;
  right: auto;
  top: 0;
}

.compare-table .table tbody tr td.pro-addtocart .add-to-cart span {
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 21px;
  overflow: hidden;
  display: block;
  text-align: center;
}

.compare-table .table tbody tr td.pro-addtocart .add-to-cart.added {
  padding-left: 25px;
  padding-right: 56px;
}

.compare-table .table tbody tr td.pro-addtocart .add-to-cart.added i {
  left: calc(100% - 41px);
}

.compare-table .table tbody tr td.pro-addtocart .add-to-cart:hover {
  color: #24bbdb;
}

.compare-table .table tbody tr td.pro-remove button {
  border: none;
  background-color: transparent;
  padding: 0;
  color: #333;
}

.compare-table .table tbody tr td.pro-remove button i {
  font-size: 20px;
}

.compare-table .table tbody tr td.pro-ratting i {
  font-size: 14px;
}

/*>>>>>>>> Login-register <<<<<<<<<*/
/*-- Login Title --*/
.login-title {
  line-height: 23px;
  text-transform: capitalize;
  font-weight: 500;
  margin-bottom: 30px;
  color: #333;
}

@media (min-width: 768px) {
  .login-title {
    font-size: 30px;
  }
}

/*-- Login Form --*/
.login-form {
  border: 1px solid #d3ced2;
  padding: 20px;
  margin: 2em 0 0;
  text-align: left;
  border-radius: 5px;
}

@media (min-width: 768px) and (max-width: 991px) {
  .login-form {
    padding: 15px;
  }
}

.login-form label {
  display: block;
  font-size: 14px;
  margin-bottom: 12px;
  font-weight: 400;
  text-transform: capitalize;
}

.login-form .nice-select {
  width: 100%;
  background-color: transparent;
  border: 1px solid #565f69;
  border-radius: 0;
  line-height: 23px;
  padding: 10px 20px;
  font-size: 14px;
  height: 45px;
  color: #d4d4d4;
  margin-bottom: 15px;
}

.login-form .nice-select::after {
  width: 6px;
  height: 6px;
  border-width: 1px;
  right: 20px;
  border-color: #565f69;
}

.login-form .nice-select .current {
  color: #555555;
  display: block;
  line-height: 23px;
}

.login-form .nice-select .list {
  width: 100%;
}

.login-form input {
  background: #f4f4f4;
  border: none;
  border-radius: 0;
  height: 50px;
  line-height: 50px;
  max-width: 100%;
  width: 100%;
  padding: 0 0 0 10px;
  vertical-align: middle;
}

.login-form input[type="checkbox"] {
  width: auto;
}

.login-form input:focus {
  border-color: #24bbdb;
  box-shadow: none;
  outline: none;
}

.login-form .check-box {
  float: left;
  margin-right: 70px;
}

.login-form .check-box:last-child {
  margin-right: 0;
}

.login-form .check-box input[type="checkbox"] {
  display: none;
}

.login-form .check-box input[type="checkbox"] + label {
  position: relative;
  padding-left: 30px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #d4d4d4;
  margin: 0;
}

.login-form .check-box input[type="checkbox"] + label::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  display: block;
  border: 2px solid #565f69;
  content: "";
  transition: all 0.3s ease 0s;
}

.login-form .check-box input[type="checkbox"] + label::after {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  content: "\f00c";
  font-family: Fontawesome;
  font-size: 12px;
  line-height: 20px;
  opacity: 0;
  color: #d4d4d4;
  width: 20px;
  text-align: center;
  transition: all 0.3s ease 0s;
}

.login-form .check-box input[type="checkbox"]:checked + label::before {
  border: 2px solid #565f69;
}

.login-form .check-box input[type="checkbox"]:checked + label::after {
  opacity: 1;
}

/*-- Place Order --*/
.register-button {
  display: block;
  margin-top: 40px;
  width: 140px;
  border-radius: 0;
  height: 36px;
  border: none;
  line-height: 24px;
  padding: 6px 20px;
  float: left;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  border-radius: 3px;
}

/*=====  End of 17. Login Register  ======*/
.entry-header {
  margin-bottom: 40px;
}

.entry-header .entry-title {
  font-size: 30px;
  line-height: 1.35;
  margin: 0 0 5px;
  text-transform: uppercase;
  color: #333;
}

@media (min-width: 992px) {
  .entry-header .entry-title {
    font-size: 36px;
  }
}

/*>>>>>>>> My-account <<<<<<<<<*/
.myaccount-tab-menu {
